import { _decorator, Component, Node, find } from 'cc';
import { MTSprite } from './MTSprite';
const { ccclass, property } = _decorator;

@ccclass('game')
export class game extends Component {
    
    private _isON : boolean = true;


    onLoad () {
        this.updateState();
    }

    onClick () {
        this._isON = !this._isON;
        this.updateState();
    }

    updateState () {
        let uinode = find('Canvas/ui');
        if (this._isON) {
            uinode.children.forEach( (node: Node) => {
                node.getComponent(MTSprite).tagAsMTSprite();
            });
        } else {
            uinode.children.forEach( (node: Node) => {
                node.getComponent(MTSprite).tagAsDefaultSprite();
            });
        }
    }
}

